.shop {
  justify-content: center;
  grid-column: 1/4;
  grid-row: 2/3;
  margin: 0 7rem 0 7rem;
  margin-top: 10px;
}

.productContainer {
  padding: 20px;
  display: grid;
  grid-auto-flow: rows;
  grid-template-columns: repeat(auto-fit, 250px);
  justify-content: center;
  gap: 30px;
}

@media (max-width: 1200px) {
  .shop {
    grid-area: main;
    margin: 0 2rem 0 2rem;
  }
}

@media (max-width: 600px) {
  .shop {
    grid-area: main;
    margin: 1rem 0.5rem 0 0.5rem;
  }
  .productContainer {
    display: flex;
    flex-direction: column;
    align-items: center;
  }
}
